import styled from 'styled-components';
import { PageTitle, H2, H3, H4, H6 } from '../../components/common/Typography';

export const Container = styled.div`
  width: 100%;
  max-width: 950px;
  padding: 80px 0;
  margin: 0 auto;

  @media screen and (max-width: 991px) {
    margin-top: -11px;
    padding: 0;
  }
`;

export const SubText = styled(H6)`
  @media screen and (max-width: 550px) {
    font-size: 14px;
  }
`;

export const AccordionContainer = styled.div`
  max-width: 1100px;
  margin: auto;
  width: 100%;
  margin-top: 42px;
  margin-bottom: 95px;
  @media screen and (max-width: 489px) {
    margin-top: 7px;
    margin-bottom: 48px;
  }
  .faq-accordion {
    .accordion-item {
      border: none;
      background-color: transparent;
      .accordion-header {
        .accordion-button {
          border-radius: 121px;
          border-bottom-right-radius: 121px !important;
          border-bottom-left-radius: 121px !important;
          padding: 38px 90px;
          background-color: #e2ae39;
          font-size: 50px;
          font-weight: 700;
          color: #000000;
          box-shadow: none !important;
          &::after {
            height: 51px;
            background-image: url('/assets/images/close_arrow.svg');
            transform: rotate(180deg);
            background-size: auto 100%;
          }
          &:not(.collapsed) {
            &::after {
              transform: rotate(0deg);
              background-image: url('/assets/images/close_arrow.svg');
            }
          }
          @media screen and (max-width: 768px) {
            font-size: 40px;
            padding: 20px 70px;
            &::after {
              height: 40px;
            }
          }
          @media screen and (max-width: 489px) {
            padding: 13px 26px;
            font-size: 24px;
            &::after {
              height: 28px;
            }
          }
        }
      }
      .accordion-collapse {
        .accordion-body {
          padding: 12px;
          .faq-item {
            padding: 47px 66px 42px 66px;
            border-bottom: 1px solid #ffffff;
            color: #ffffff;
            h5 {
              margin-bottom: 30px;
              line-height: 1.4;
            }
            p {
              line-height: 1.2;
            }
            @media screen and (max-width: 768px) {
              padding: 30px;
            }

            @media screen and (max-width: 550px) {
              padding: 11px 15px 10px 15px;
              h5 {
                font-size: 16px;
                margin-bottom: 16px;
              }
              p {
                font-size: 14px;
              }
            }
          }
        }
      }
    }
  }
`;

export const TeamContainer = styled.div`
  max-width: 1040px;
  margin: 2rem auto 0;
  display: flex;
  justify-content: center;
  align-items: start;
  flex-wrap: wrap;
  gap: 60px;
  padding: 50px 0;
  @media screen and (max-width: 500px) {
    padding: 30px 0;
    margin: auto;
  }
`;

export const TeamInnerWrapper = styled.div`
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 57px;

  @media screen and (max-width: 576px) {
    padding: 0;
    gap: 24px;
  }
`;

export const ImageContainer = styled.div`
  width: 191px;
  height: 191px;
  border-radius: 41px;
  overflow: hidden;
  margin-bottom: 12px;

  img {
    width: 100%;
    height: 100%;
  }

  @media screen and (max-width: 576px) {
    width: 112px;
    height: 112px;
    border-radius: 24px;
  }
`;

export const Row = styled.div`
  border-top: 3px solid #716161;
  transform: matrix(1, 0, 0, 1, 0, 0);
  padding: 28px 0;

  &:last-of-type {
    border-bottom: 3px solid #716161;
  }
`;

export const DetailSection = styled.section`
  max-width: 1060px;
  margin: 130px auto;

  @media screen and (max-width: 576px) {
    margin: 60px 40px;
  }

  & > ul {
    padding-left: 2rem;
    font-size: 1.5em;

    & > ul {
      padding-left: 4rem;
    }

    @media screen and (max-width: 576px) {
      font-size: 12px;
    }
  }
`;
