const MemberComponent = ({ img, name = '' }: MemberComponentProps): JSX.Element => {
  return (
    <div className="d-flex flex-column align-items-center">
      <ImageContainer>
        <img src={img} alt={name} />
      </ImageContainer>
      <H4 fontFamily="'Barlow Condensed', sans-serif" fontWeight={700}>
        {name}
      </H4>
    </div>
  );
};

